/*---------------- SITE RESETS ----------------*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

/* This removes the top shadow iOS adds to all input areas */
input[type="text"],
input[type="email"],
input[type="search"],
input[type="password"] {
  -webkit-appearance: none;
  -moz-appearance: none; /* mobile firefox too! */
}

::-moz-selection {
  background-color: #c7ddf8;
  text-shadow: none;
}

::selection {
  background-color: #c7ddf8;
  text-shadow: none;
}
::-moz-selection {
  background-color: #c7ddf8;
  text-shadow: none;
}
:target,
mark {
  background-color: rgba(199, 221, 248, 0.3);
}

input {
  padding: 0;
  margin: 0;
  border-radius: 0;
  border-radius: 0;
}
input,
textarea,
select {
  -webkit-appearance: none;
}

body {
  line-height: 1;
  font-family: "smythesansregular";
  background-color: #e0dce6;
}
ol,
ul {
  list-style: none;
}

/*---------------- TEXT STYLES ----------------*/
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "smythesansregular";
  margin-bottom: 12px;
  text-align: left;
  line-height: 100%;
}

h1 {
  font-size: 40px;
  color: #35a393;
  line-height: 120%;
}
h2 {
  font-size: 36px;
  padding-bottom: 6px;
}
h3 {
  font-size: 24px;
  margin: 60px 0 20px 0;
}

h4 {
  font-size: 20px;
  margin-top: 20px;
}

p {
  font-family: "Graphik LCG Regular";
  size: 16px;
  line-height: 160%;
  margin-bottom: 32px;
  text-align: left;
}
sub {
  font-size: 10px;
  bottom: 0;
}

.chart {
  margin-bottom: 60px;
  margin-top: 50px;
}
/*---------------- GENERAL STYLES ----------------*/

.opacity-50 {
  opacity: 0.6;
}
.margin-10 {
  margin-bottom: 10px;
}
.button {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 10px 16px;
  margin: 20px 0 0 20%;
  border-radius: 8px;
  font-size: 20px;
  background-color: #0054a6;
  border: 1px solid #ffffff;
  color: #ffffff;
  font-family: "Graphik LCG Regular";
}
a.button:hover {
  cursor: pointer;
  border: 1px solid rgb(68, 68, 68);
}
.header-button {
  margin: 0px;
  margin-top: 20px;
}

/*---------------- NAVIGATION ----------------*/

.show,
.collapsing {
  background-color: rgb(233, 233, 233);
}
.navbar {
  width: 100%;
  padding: 0;
}
.nav-link {
  font-family: "cerapro-regular";
  color: #434343;
}
.nav-link:hover {
  color: #000000;
  cursor: pointer;
}
.navigation {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}
.navigation ul {
  text-decoration: none;
  margin: 0;
  padding: 0;
  -webkit-flex: content;
          flex: content;
  -webkit-align-content: flex-end;
          align-content: flex-end;
}
.navigation ul li {
  font-size: 18px;
  list-style: none;
  text-align: left;
  display: inline-block;
  padding: 0;
  margin: 0;
  margin-left: 38px;
  line-height: 0;
}

/*---------------- HEADER ----------------*/

.header {
  width: 100%;
  margin: auto;
  background: #f7f8fa no-repeat;
  background-image: url(/degree-debt/static/media/header-background.547cda53.jpg);
  background-size: 90%;
  background-position: bottom right;
}
.header-content {
  max-width: 70%;
  padding: 16px 5% 0 5%;
  margin: 0 auto;
  display: -webkit-flex;
  display: flex;
}

#top-logo {
  display: inline-block;
  height: 28px;
  width: 180px;
  background-size: 100%;
  background-repeat: no-repeat;
  background-image: url(data:image/png;base64,);
}

#header-title {
  max-width: 70%;
  width: 100%;
  margin: 60px auto 0 auto;
  padding: 0 5% 80px 5%;
  text-align: left;
}

.banner {
  width: 100%;
  height: auto;
  background-color: #4cbdad;
  overflow: hidden;
}
.banner-content p,
.banner-content h2 {
  color: #ffffff;
}

.banner-content {
  max-width: 70%;
  width: 100%;
  margin: 56px auto 0 auto;
  padding: 0 5% 30px 5%;
  text-align: left;
}

.bannerText {
  color: #ffffff;
}
.bannerText p {
  margin-bottom: 40px;
}

.aboutColor {
  background-color: #4cbdad;
}

/*---------------- PAGE GLOBALS ----------------*/
.page-wrapper {
  width: 100%;
  padding-bottom: 40px;
}
.page-content {
  max-width: 70%;
  margin: 0 auto;
  padding: 20px 5% 0 5%;
}
.separator {
  height: 2px;
  width: 100%;
  padding-bottom: 10px;
  background-repeat: repeat-x;
  background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAoAAAACCAQAAADLaIVbAAAAEElEQVR4XmNgSMMC/5/BhAA4/xXr6v6APQAAAABJRU5ErkJggg==);
}
.clear {
  clear: both;
}
.article-image {
  display: inline-block;
  padding: 0 14px 10px 0;
}

.learn-photo {
  display: -webkit-inline-flex;
  display: inline-flex;
}

/*---------------- DEBT CALCULATOR ----------------*/

#debt-calculator {
  max-width: 600px;
  width: 100%;
  margin: 0 auto;
}
#debt-calculator p {
  width: 100%;
  text-align: left;
  margin: 20px 0 4px 0;
}

/*---------------- HERO IMAGE ----------------*/
.floating-image {
  margin-top: 50px;
}
#coffee-cup {
  width: 235px;
  height: 196px;
  float: left;
  background-repeat: no-repeat;
  background-image: url(/degree-debt/static/media/coffee-cup.d2e73533.png);
  background-size: 235px 196px;
}
#paper-clips {
  width: 212px;
  height: 164px;
  float: right;
  background-repeat: no-repeat;
  background-image: url(/degree-debt/static/media/paper-clips.6ab1c20b.png);
  background-size: 212px 164px;
}

/*---------------- FOOTER ----------------*/
#footer-content {
  width: 70%;
  margin: 0 auto;
  padding: 0 5% 16px 5%;
}
.footer-navigation {
  height: 20;
  margin-left: auto;
  float: right;
}

.footer-navigation .nav-link {
  padding: 0;
  margin: 0;
}
.footer-navigation ul {
  text-decoration: none;
  margin: 0;
  padding: 0;
  text-align: right;
}
.footer-navigation ul li {
  font-size: 14px;
  list-style: none;
  display: inline-block;
  padding: 0px;
  line-height: 140%;
  margin: 4px 0 0 26px;
}
#footer-logo {
  height: 20px;
  width: 150px;
  margin-top: 4px;
  float: left;
  background-size: 130px 20px;
  background-repeat: no-repeat;
  background-position: left center;
  background-image: url(data:image/png;base64,);
}

/*---------------- FORM STYLE ----------------*/
.form-input {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 15px;
  position: relative;
}
.form-input .button {
  font-size: 16px;
  float: right;
}
.form-input input[type="submit"]:focus {
  outline: none;
}
label {
  width: 100%;
  text-align: left;
}

.form-input input[type="text"] {
  background-color: #fafafa;
  width: 100%;
  border-top: 1px solid #d0d2d7;
  border-bottom: 1px solid #ffffff;
  border-left: none;
  border-right: none;
  padding: 12px 12px 8px 12px;
  border-radius: 8px;
  font-size: 14px;
  margin: 10px 0 16px 0;
}
.form-input input::-webkit-input-placeholder {
  color: #999999;
}
.form-input input:focus::-webkit-input-placeholder {
  color: #e3e3e3;
}
/* Firefox < 19 */
.form-input input:-moz-placeholder {
  color: #999999;
}
