/*---------------- MEDIA QUERIES ----------------*/

/* RETINA QUIRIES */
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
  #top-logo {
    background-image: url(data:image/png;base64,);
  }
  #footer-logo {
    background-image: url(data:image/png;base64,);
  }
  #coffee-cup {
    background-image: url(/degree-debt/static/media/coffee-cupX2.e5a8982f.png);
  }
  #paper-clips {
    background-image: url(/degree-debt/static/media/paper-clipsX2.8ee5e5a2.png);
  }
}

/* RESIZING MEDIA QUIRIES */

/* Extra small devices (phones, 1200px and down) */

@media only screen and (max-width: 1400px) {
  #coffee-cup {
    display: block;
    position: relative;
    left: -4%;
    overflow: hidden;
  }
  #paper-clips {
    display: block;
    position: relative;
    right: -4%;
    overflow: hidden;
  }
}
@media only screen and (max-width: 1200px) {
  #header-title,
  .banner-content,
  .header-content,
  .page-content {
    max-width: 100%;
  }

  #footer-content {
    width: 100%;
    padding: 0 5% 16px 5%;
  }
  #paper-clips {
    display: none;
  }
}

@media only screen and (max-width: 1000px) {
  #coffee-cup {
    display: none;
  }
  #header-title {
    margin-top: 40px;
  }
  .banner-content {
    margin: 40px auto;
    padding-bottom: 0;
  }
}

@media only screen and (max-width: 768px) {
  #footer-logo {
    float: none;
    display: block;
    margin-bottom: 5px;
  }
  .footer-navigation ul li {
    display: block;
    text-align: left;
    margin: 0;
    padding: 0;
  }
  .footer-navigation ul li .nav-link {
    margin: 0;
    padding: 0;
  }
  .footer-navigation {
    float: none;
    margin: 0;
    padding: 0;
  }
}

@media only screen and (max-width: 600px) {
  #coffee-cup {
    display: none;
  }
  #paper-clips {
    display: none;
  }
}

/* Hack to remove Safari's extra padding. */
@media screen and (-webkit-min-device-pixel-ratio: 0) {
  .placeholder input,
  .placeholder textarea {
    padding: 4px;
  }
}

